import { useContext, useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import { ProjectListContext } from "../components/Content";

// TODO :   

//  
//  

const ProjectView = () => {
    const { id } = useParams();
    const projectList = useContext(ProjectListContext);
    const navigate = useNavigate();
    const [data, setData] = useState();

    useEffect(() => {
        const titleElement = document.getElementsByTagName('title')[0];
        titleElement.innerHTML = 'ProjectView';
    }, []);

    useEffect(() => {
        if (projectList.length >= 1) {
          const targetProject = projectList.find(
            (it) => parseInt(it.id) === parseInt(id)
          );
    
          if (targetProject) {
            //   
            setData(targetProject);
          } else {
            //   
            alert("  .");
            navigate("/project", { replace: true });
          }
        }
      }, []);

      if (!data) {
        return <div className="DiaryPage">...</div>;
      }

      //console.log(data);

    return (
        <div className="ProjectView">
            <div className="project-img">
                <div className="title">
                    <em>{data.type}</em>
                    <h2>{data.title}</h2>
                </div>
            </div>

            <div className="project-info">
                <h3 className="title">{data.name}</h3>
                <em>{data.description}</em>
            </div>

            <div className="project-detail">
                <div>
                    <strong></strong>
                    <p>{data.composition}</p>
                </div>

                <div>
                    <strong></strong>
                    <p>{data.participationRate}</p>
                </div>

                <div>
                    <strong></strong>
                    <p>{data.technicalInfo}</p>
                </div>

                <div>
                    <strong></strong>
                    <a href={data.link} target="_blank" rel="noopener noreferrer"> </a>
                </div>
            </div>

            <div className="aa">
                <Link to={'/project'} >
                    <span>&#xE000;</span> 
                </Link>
            </div>
            
        </div>
    );
};

export default ProjectView;